import React, {Component} from 'react';
import {render} from 'react-dom';
import injectTapEventPlugin from 'react-tap-event-plugin';
import ReactPull from '../js/ReactPull';
import ReactCarousel from '../js/ReactCarousel';
import '../sass/pull.scss';
import '../sass/carousel.scss';
import './sass/example.scss';

//  tapEvent , 
injectTapEventPlugin();

class ReactPullExample extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: 30,
      disablePullUp: false
    };
  }

  refreshCallback = () => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        let result = false;
        if (Math.random() > 0.2) {
          result = true;
        }
        if (result) {
          this.setState({
            items: 30,
            disablePullUp: false
          }, () => {
            resolve();
          });
        } else {
          reject(new Error(''));
        }
      }, 1000);
    }).then(() => {
      console.info('');
    }, () => {
      console.info('');
    });
  };

  loadMoreCallback = () => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        let result = false;
        if (Math.random() > 0.2) {
          result = true;
        }
        if (result) {
          this.setState({
            items: this.state.items + 10,
            disablePullUp: this.state.items >= 60
          }, () => {
            resolve();
          });
        } else {
          reject(new Error(''));
        }
      }, 1000);
    }).then(() => {
      console.info('');
    }, () => {
      console.info('');
    });
  };

  handleTouchTap = (e) => {
    console.info(' Tap ');
  };

  render() {
    const contents = [];
    const {items, disablePullUp} = this.state;

    for (let i = items; i > 0; i--) {
      if (i < 10) {
        contents.push(<li key={i}><a href="http://www.sina.com">DOM</a> {i}</li>);
      } else {
        contents.push(<li key={i} onTouchTap={this.handleTouchTap}>DOM {i}</li>);
      }
    }

    const props = {
      refreshCallback: this.refreshCallback,
      loadMoreCallback: this.loadMoreCallback,
      refresh: true,
      loadMore: true,
      disablePullUp,
    };

    const carouselItems = [{
      image: 'http://alloyteam.github.io/AlloyTouch/example/asset/ci1.jpg',
      link: 'http://jd.com'
    }, {
      image: 'http://alloyteam.github.io/AlloyTouch/example/asset/ci2.jpg',
    }, {
      image: 'http://alloyteam.github.io/AlloyTouch/example/asset/ci3.jpg',
      link: 'http://jd.com'
    }, {
      image: 'http://alloyteam.github.io/AlloyTouch/example/asset/ci4.jpg'
    }];

    return (
      <ReactPull {...props} className="pull-carousel" refreshThreshold={60}>
        <div>
          <ReactCarousel items={carouselItems}/>
          <ol className="example-list">
            {contents.map((item) => {
              return item;
            })}
          </ol>
        </div>
      </ReactPull>
    );
  }
}

render(
  <ReactPullExample />, document.getElementById('layout')
);



