import React, {Component} from 'react';
import PropTypes from 'prop-types';
import AlloyTouch from './AlloyTouch';
import Transform from 'alloytouch/transformjs/transform';

//  https://github.com/AlloyTeam/AlloyTouch 
class ReactAlloyTouch extends Component {
  //
  static propTypes = {
    className: PropTypes.string, //  className
    prefix: PropTypes.string, // 
    options: PropTypes.object, // AlloyTouch 
    items: PropTypes.array, // 
    active: PropTypes.number, // 
    autoPlay: PropTypes.oneOfType([PropTypes.bool, PropTypes.number]), // 
    events: PropTypes.object, // 
  };

  static defaultProps = {
    className: '',
    prefix: 'carousel',
    active: 0,
    autoPlay: 4000, // 4
    events: {},
  };

  constructor(props) {
    super(props);

    this.currentIndex = 0;
  }

  componentDidMount() {
    const {wrapper, scroller, nav} = this.refs;
    const {options, items} = this.props;

    this.navItems = nav.querySelectorAll('a');
    Transform(scroller, true);

    const alloyOptions = {
      touch: wrapper, //dom
      vertical: false, //truetouch
      target: scroller, //
      property: 'translateX',  //
      min: wrapper.clientWidth * -(items.length - 1), //,
      max: 0, //,
      step: wrapper.clientWidth,
      spring: true, //,true
      inertia: false, //,true
      touchStart: this.touchStart,
      touchEnd: this.touchEnd,
      animationEnd: this.animationEnd,
      ...(options || {})
    };

    //  alloyTouch 
    this.alloyTouch = new AlloyTouch(alloyOptions);
    this.handleAutoPlay();
  }

  touchStart = () => {
    // 
    clearInterval(this.timerId);
  };

  touchEnd = (evt, value, index) => {
    const {step, min, max} = this.alloyTouch;
    const stepV = index * step * -1;
    const dx = value - stepV;

    if (value < min) {
      this.alloyTouch.to(min);
    } else if (value > max) {
      this.alloyTouch.to(max);
    } else if (Math.abs(dx) < 30) {
      this.alloyTouch.to(stepV);
    } else if (dx > 0) {
      this.alloyTouch.to(stepV + step);
    } else {
      this.alloyTouch.to(stepV - step);
    }
    this.currentIndex = index;

    // 
    this.handleAutoPlay();
    return false;
  };

  animationEnd = () => {
    const len = this.navItems.length;
    let i = 0;
    for (; i < len; i++) {
      if (i === this.alloyTouch.currentPage) {
        this.navItems[i].classList.add('active');
      } else {
        this.navItems[i].classList.remove('active');
      }
    }
  };

  // 
  handleAutoPlay() {
    const {autoPlay, items} = this.props;
    const len = items.length;
    if (autoPlay) {
      this.timerId = setInterval(() => {
        const {step} = this.alloyTouch;
        const stepV = this.currentIndex * step * -1;
        this.alloyTouch.to(stepV);
        if (this.currentIndex >= len - 1) {
          this.currentIndex = 0;
        } else {
          this.currentIndex++;
        }
      }, autoPlay);
    }
  }

  render() {
    const {
      className, prefix, items, active, events,
    } = this.props;

    const len = items.length;
    return (
      <div className={`${prefix}-wrapper ${className || ''}`} ref="wrapper">
        <div className={`${prefix}-scroller`} ref="scroller" style={{width: `${100 * len}%`}}>
          {items.map(({image, link}) => {
            if (link) {
              return (<a key={image} href={link} {...events}>
                <img style={{width: `${100 / len}%`}} src={image}/>
              </a>);
            }
            return (<img key={image} style={{width: `${100 / len}%`}} src={image} {...events}/>);
          })}
        </div>
        <div className={`${prefix}-nav`} ref="nav">
          {items.map((it, index) => {
            /*eslint-disable react/no-array-index-key*/
            return (<a key={index} className={active === index ? 'active' : ''}></a>);
          })}
        </div>
      </div>
    );
  }
}

export default ReactAlloyTouch;



